.pick-address{
  border-right: 1px solid #3f3f3f;
    padding-left: 70px;
    padding-right: 69px;
    display: inline-block;
    vertical-align: middle;
    cursor: pointer;
    width: 19.79%;
    overflow: hidden;
}
.form-booking label {
  font-size: 16px;
  color: #999;
  display: block;
  position: relative;
}
.form-booking .info {
  font-size: 16px;
  color: #999;
  display: block;
  position: relative;
  margin-bottom: 10px;
}
.form-booking input::placeholder{
  color: #fff;
}
.form-booking input, .form-booking select {
  font-size: 18px;
  line-height: 23px;
  color: #e3e3e3;
  padding: 0;
  margin: 0;
  border: 0px;
  outline: none;
  height: auto;
  background-color: transparent;
  /* font-family: 'Dosis'; */
  max-width: 100%;
  appearance: none;
}

 .form-booking > div:nth-child(3) label:after {
  content: "";
  border: solid 5px transparent;
  border-top: solid 5px #bf9c60;
  position: absolute;
  right: -10px;
  top: 39px;
}
.form-booking > div:nth-child(4) label:after {
  content: "";
  border: solid 5px transparent;
  border-top: solid 5px #bf9c60;
  position: absolute;
  right: -10px;
  top: 39px;
}
.form-booking{
  position: absolute;
  bottom: 0;
  width: 100%;
  display: flex;
  background-color: #2c2c2ce6;
  overflow: hidden;
  justify-content: space-between;
  align-items: center;

}
.Modal_form .SideLogo{
  margin-top: 20px;
  margin-bottom: 205px;
  text-align: center;
}
.TopCartbox{
  background-color: #2c2c2c;
  transition: all .5s ease-in-out;
  transform: translateY(-100%);
