.booking-steps-area {
  padding: 38px 0 17px;
  border-bottom: 1px solid #ebe9dc;
  position: relative;
}
ul.booking-steps li {
  display: inline-block;
  position: relative;
  margin-right: 26px;
  cursor: pointer;
}
ul.booking-steps li span {
  position: absolute;
  top: -15px;
  left: 40px;
  height: 32px;
  width: 32px;
  background: #ebe8de;
  color: #fff;
  font-weight: bold;
  text-align: center;
  line-height: 32px;
  border-radius: 50%;
  z-index: 9;
}
ul.booking-steps li.active span, ul.booking-steps li:hover span {
  background: #1e1e1e;
}
ul.booking-steps li .icon {
  float: left;
  margin-right: 30px;
  position: relative;
  height: 70px;
  width: 70px;
  line-height: 70px;
  text-align: center;
  border: 1px solid #f3f3f3;
  border-radius: 50%;
}
ul.booking-steps li:hover .icon, ul.booking-steps li.active .icon {
  background: #f3f3f3;
}
ul.booking-steps li .text {
  overflow: hidden;
  line-height: 70px;
  font-size: 18px;
}
ul.booking-steps li.active .text, ul.booking-steps li:hover .text {
  color: #1e1e1e;
}
