.ServiceSection {
    width: 100%;
    height: auto;
    padding: 50px 0px;
}

.ServiceSection .row {
    margin-top: 40px;
    row-gap: 30px;
}

.servicePageimg {
    width: 100%;
    height: 230px;
    overflow: hidden;
    border-radius: 6px;
}

.servicePageimg img {
    width: 100%;
    height: 100%;
    transition-property: all;
    transition-duration: 700ms;
}
.servicePageimg img:hover{
    transform: scale(1.2, 1.2);
}

.sarvicepageTitle h4 {
    font-weight: 400;
    line-height: 30px;
    color: #1e1e1e;
    font-size: 23px;
    text-align: center;
    padding: 10px 0px 20px;
}

.servicePageCard:hover {
    box-shadow: 0px 0px 5px 0px #ccc;
    border-radius: 6px;
    transition-property: all;
    transition-duration: 700ms;
}
.servicePageCard:hover .sarvicepageTitle h4 {
    color: #f28b00;
}

@media(max-width:991px){
    .servicePageimg{
        height: 200px;
    }
    .sarvicepageTitle h4{
        font-size: 19px;
        padding: 6px 0px 10px;
    }
    .ServiceSection .row{
        row-gap: 16px;
    }
    .ServiceSection{
        padding: 30px 0px;
    }
}
