.carClassSection{
    width: 100%;
    height: auto;
    margin: 50px 0px;
}
.header2{
    width: 100%;
    height: 95px;
    background: #1e1e1e;

}
.bradecomeSection{
    width: 100%;
    height: auto;
    padding: 40px 30px 0px;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    gap: 10px;
}

.bradecomeSection a{
   font-size: 18px;
   color: #1e1e1e;
   font-weight: 600;
}
.bradecomeSection .active{
    color: #bf9c60;
}
.minmaxSection{
    width: 100%;
    height: auto;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    gap: 60px;
    margin-bottom: 20px;
}
.maxsection{
    display: flex;
    align-items: center;
    justify-content: flex-start;
    gap: 10px;
}
.maxsection i{
    font-size: 16px;
    color: #bda777;
}
.maxsection span{
    font-size: 16px;
    color: #b2adad;
}
.PriceSection{
    width: 100%;
    height: auto;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    gap: 60px;
    margin: 10px;
}
.priceBtn{ 
       outline: none;
    border: none;
    cursor: pointer;
    text-decoration: none;
    height: 50px;
    width: 170px;
    /* padding: 0 85px 0 48px; */
    color: #fff;
    background: #bf9c60;
    border-radius: 4px;
    font-size: 17px;
    text-transform: uppercase;
    letter-spacing: 1px;
    transition-property: all;
    transition-duration: 700ms;
}
.priceBtn i{
    margin-left: 20px;
}
.priceBtn:focus{
    outline: none;
    border: none;
}
.priceBtn:hover{
    background: #1e1e1e;
}

@media(max-width:991px){
    .priceBtn{
        height: 42px;
        width: 145px;
    }
}