* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  margin: 0;
  max-width: 100%;
  overflow-x: hidden;
  line-height: 1.6;
  letter-spacing: 0.5px;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0;
  font-family: 'Abel', sans-serif  !important;
}

a,
p,
ul {
  margin: 0;
  padding: 0;
  font-family: 'Abel', sans-serif !important;
  list-style-type: none;
}

.custContain {
  max-width: 1200px;
  width: 100%;
  margin: 0 auto;
  padding: 0 20px;
}

a {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

/* header */
.header {
  width: 100%;
  height: auto;
  padding: 20px 0px;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 2;
  background-color: transparent;
}

.headerContain {
  width: 100%;
  height: auto;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.headetLeft {
  width: 20%;
  height: auto;
}

.headerbtn {
  outline: none;
  border: none;
  text-decoration: none;
  cursor: pointer;
  background-color: transparent;
  font-size: 35px;
  color: #ffffff;
}

.headerbtn:focus {
  outline: none;
  border: none;
}

/* .headerLogo {
  width: 150px;
  height: auto;
}

.headerLogo img {
  width: 100%;
  height: 100%;
} */
.headerLogo{
  width: 60%;
  text-align: center;
}
.headerLogo h2 {
  font-family: 'Charm', cursive !important;
  color: #fff;
  font-size: 43px;
  text-transform: capitalize;
}
.headerLogo h2 span{
  color: #d7d2bd;
}

.headerRight {
  width: 20%;
  height: auto;
  display: flex;
  align-items: center;
  justify-content: end;
  gap: 10px;
}

.headerRight a {
  font-size: 18px;
  color: #ffffff;
}

.headerRight a i {
  margin-right: 10px;
}

.SideBar {
  position: fixed;
  top: 0;
  left: -100%;
  right: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 99;
}

.SideBar.active {
  left: 0;
}

.SideBarSection {
  position: absolute;
  top: 0;
  left: 0;
  max-width: 400px;
  width: 100%;
  height: 100%;
  background-color: #1e1e1e;
  transition-property: all;
  transition-duration: 600ms;
}

.sidebarHeader {
  width: 100%;
  height: auto;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 20px;
}

.SideLogo h2{
  font-family: 'Charm', cursive !important;
  color: #fff;
  font-size: 33px;
  text-transform: capitalize;
}
.SideLogo h2 span{
  color: #d7d2bd;
}
.CLOSEbTN {
  outline: none;
  border: none;
  cursor: pointer;
  background: transparent;
  text-decoration: none;
  color: #ffffff;
  font-size: 23px;
}
.CLOSEbTN:focus{
  outline: none;
  border: none;
}
.SideManu a li {
  font-size: 25px;
  color: #969696;
  padding: 15px 20px;
  width: 100%;
}

.SideManu a li:hover {
  color: #ffffff;
  border-left: 1px solid #fff;
  margin-left: 10px;
}

.BookBtn {
  margin: 40px 20px;
}

.BookingBtn {
  outline: none;
  border: 1px solid #d7d2bd;
  text-decoration: none;
  cursor: pointer;
  width: 195px;
  height: 50px;
  color: #d7d2bd;
  font-size: 14px;
  letter-spacing: 1px;
  background-color: transparent;
  transition-property: all;
  transition-duration: 600ms;
}

.BookingBtn i {
  padding-left: 10px;
}

.BookingBtn:focus {
  outline: none;
  border: none;
}

.BookingBtn:hover {
  background-color: #d7d2bd;
  color: #1e1e1e;
}

/* Footer */

.footer {
  width: 100%;
  height: auto;
  padding: 50px 0px;
  background: #1e1e1e;
}

.FooterLogo h2{
  font-family: 'Charm', cursive !important;
  color: #fff;
  font-size: 40px;
  text-transform: capitalize;
  margin-bottom: 15px;
}
.FooterLogo h2 span{
  color: #d7d2bd;
}

.FooterManu p {
  font-size: 17px;
  font-weight: 400;
  color: #969696;
  margin-bottom: 7px;
}

.FooterManu a {
  font-size: 17px;
  font-weight: 400;
  color: #969696;
  margin-bottom: 7px;
  display: flex;
  flex-direction: column;
}

.FooterLink {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  gap: 20px;
  margin-top: 10px;
}

.FooterLink a {
  font-size: 17px;
  color: #969696;
}

.FooterTitle h4 {
  font-size: 25px;
  color: #fffffe;
  margin-bottom: 30px;
}

.SubFooter {
  width: 100%;
  height: auto;
  padding: 20px 0px;
  background-color: #0e0e0e;
  border-top: 1px solid #393939;
}

.CopyRight p {
  text-align: center;
  color: #969696;
  font-size: 14px;
}







@media(max-width:991px) {
  .headerRight a {
    font-size: 16px;
  }
  .headerbtn{
    font-size: 30px;
  }
  /* .headerLogo {
    width: 120px;
  } */
  .headerLogo h2{
    font-size: 37px;
  }
}


@media(max-width:767px) {
  .footer {
    padding: 30px 0px;
  }

  .footer .row {
    gap: 20px;
  }

  .footerLeft {
    text-align: center;
  }

  .footerLogo {
    margin: 0 auto 30px;
  }

  .FooterLink {
    justify-content: center;
  }
}
@media(max-width:575px){
  .headerLogo h2 {
    font-size: 27px;
}
}
@media(max-width:480px){
  .headetLeft{
    width: 10%;
  }
  .SideManu a li {
    font-size: 20px;
  }
  .SideLogo h2{
    font-size: 25px;
  }
  .CLOSEbTN{
    font-size: 17px;
  }
  .headerLogo h2 {
    font-size: 18px;
}
.headerRight{
  width: 40%;
}
.headerLogo {
  width: 50%;
}
.SideBarSection{
  max-width: 290px;
}
}