.My_booking .leftCnt .price {
  font-size: 30px;
  color: #000;
}
.My_booking .leftCnt p {
  font-size: 16px;
  color: #b2adad;
  line-height: 26px;
  margin: 20px 0;
  text-align: justify;
}
.My_booking .leftCnt h4 {
  font-size: 30px;
  color: #000;
  font-weight: bold;
  margin-bottom: 10px;
}
.My_booking .leftCnt h6 {
  font-size: 18px;
  color: #000;
  font-weight: 500;
}
.My_booking {
  padding: 50px 0;
}
.My_booking .image {
  width: 100%;
  height: 250px;
  margin-left: auto;
}
.My_booking .image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.My_booking .row {
  padding: 20px;
  margin-bottom: 30px;
}
.My_booking .row:hover {
  border: 1px solid #bf9c60;
  border-radius: 6px;
}

@media (max-width: 767px) {
    .My_booking .leftCnt h6{
        font-size: 14px;
    }
  .My_booking .image {
    margin: 10px 0;
    height: auto;
  }
  .My_booking{
    padding: 30px 0;
  }
  .My_booking .row{
    padding: 15px;
  }
}
