.AboutContain {
    width: 100%;
    height: auto;
    padding: 50px 0px;
}

.AboutContainDtls {
    margin-top: 40px;
}

.AboutImg {
    width: 600px;
    height: 450px;
    float: left;
    margin: 0 26px 17px 0;
    border-radius: 8px;
    box-shadow: 9px 11px 16px #0000004a;
}

.AboutImg img {
    width: 100%;
    height: 100%;
}

.aboutDtls p {
    margin-bottom: 20px;
    color: #969696;
    line-height: 30px;
    font-size: 18px;
}

.ourCompany h3 {
    margin-bottom: 15px;
    font-size: 20px;
    font-weight: 400;
    line-height: 30px;
    color: #1e1e1e;
}

@media(max-width:991px) {
    .AboutImg {
        width: 450px;
        height: 350px;
    }

    .AboutContain {
        padding: 40px 0px 30px;
    }
}

@media(max-width:767px) {
    .AboutImg {
        width: 315px;
        height: 250px;
    }

    .AboutContain {
        padding: 30px 0px 0px;
    }

    .AboutContainDtls {
        margin-top: 30px;
    }
}

@media(max-width:575px) {
    .AboutImg {
        width: 240px;
        height: 220px;
    }

    .aboutDtls p {
        line-height: 27px;
        font-size: 16px;
        margin-bottom: 12px;
    }
}
@media(max-width:480px){
    .AboutImg {
        width: 100%;
        height: 230px;
        margin: 0 auto 22px;
    }
    .AboutContainDtls {
        margin-top: 20px;
    }
}