.header2{
    width: 100%;
    height: 95px;
    background: #1e1e1e;

}
.checkoutPage{
    max-width: 950px;
    height: auto;
    padding: 50px 0px;
    margin: 0 auto;
}
.thankyouSection{
    width: 100%;
    height: auto;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 20px;
    margin-bottom: 33px;
}
.thankyouImg{
    width: 46px;
    height: 46px;
}
.thankyouImg img{
    width: 100%;
    height: 100%;
}
.thankyouSection h5{
    margin-top: 10px;
    color: #1e1e1e;
    font-size: 30px;
}
.thanksContain p{
    line-height: 30px;
    color: #969696;
    text-align: center;
}

.checkoutMiddle{
    margin-top: 50px;
    background: #f3f3f3;
    padding: 100px 0px;
    margin-bottom: 37px;
    text-align: center;
}
.checkoutMiddle h2 {
    font-size: 30px;
    margin-bottom: 49px;
    text-transform: uppercase;
}

.checkoutMiddle ul li {
    width: 100%;
    margin-bottom: 15px;
}

.checkoutMiddle .summary-bar li .info {
    color: #1e1e1e;
    line-height: 23px;
    font-size: 17px;
}
.checkoutMiddle .summary-bar li p {
    color: #969696;
    line-height: 24px;
}
.selectCar{
    width: 100%;
    height: auto;
    text-align: center;
}
.selectCar h3{
    font-size: 30px;
    margin-bottom: 17px;
    text-transform: uppercase;
}
.selectCar p{
    font-size: 20px;
    color: #969696;
    line-height: 30px;
}
.select_carimg{
    max-width: 600px;
    height: 300px;
    margin: 0 auto;
}
.select_carimg img{
    width: 100%;
    height: 100%;
}


@media(max-width:480px){
    .checkoutMiddle{
        padding: 60px 0px;
    }
    .checkoutMiddle h2 {
        font-size: 26px;
    }
    .select_carimg {
        max-width: 100%;
        height: 192px;
    }
    .selectCar h3 {
        font-size: 27px;
        margin-bottom: 7px;
    }
    .selectCar p {
        font-size: 17px;
    }        
}