.contactBannerSection{
    width: 100%;
    height: auto;
    position: relative;
}
.ContactBanner{
    width: 100%;
    height: 500px;
}
.ContactBanner img{
    width: 100%;
    height: 100%;
}
.contctOverlay{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #000000a8;
}

/* Contact Us Page */
.contactPageMain {
    width: 100%;
    height: auto;
    padding: 30px 0px;
}

.ContactTitle h3 {
    font-size: 34px;
    font-weight: 600;
    text-transform: capitalize;
    letter-spacing: 1.7px;
    color: #1e1e1e;
    text-align: center;
    position: relative;
}

.ContactTitle p {
    /* margin-top: 22px; */
    font-size: 25px;
    /* font-weight: 600; */
    color: #adaba5;
    text-align: center;
    letter-spacing: 1.3px;
}

.contactMainSection {
    margin-top: 60px;
    width: 100%;
    height: auto;
}

.contactUsTitle h3 {
    font-size: 32px;
    font-weight: 600;
    color: #bf9c60;
    margin-bottom: 20px;
    text-transform: uppercase;
}

.contactLeft {
    width: 100%;
    height: auto;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    gap: 15px;
    margin-bottom: 10px;
}

.contactLeft p {
    font-size: 19px;
    color: #1e1e1e;
    font-weight: 500;
}

.contactLeft i {
    color: #1e1e1e;
}

.contmap iframe {
    width: 100%;
    height: 300px;
    border: none;
    outline: none;
    margin-top: 20px;
    border: 5px solid #d7d2bd;
    border-right: none;
    border-radius: 8px;
}

.contmap iframe:focus {
    outline: none;
    border: none;
    box-shadow: none;
}

.contactMainSection .row {
    justify-content: space-between;
}

.contactUsFromTitle h3 {
    font-size: 32px;
    font-weight: 600;
    color: #bf9c60;
    margin-bottom: 30px;
    text-align: center;
    text-transform: uppercase;
}

.fromInput input {
    width: 100%;
    height: auto;
    outline: none;
    border-radius: 10px;
    border: 1px solid #d7d2bd;
    background: #FFF;
    padding: 12px;
    color: #000;
    font-family: Plus Jakarta Sans;
    font-size: 16px;
    font-style: normal;
    font-weight: 500;
    line-height: 150%;
    letter-spacing: 0.48px;
    margin-bottom: 15px;
}

.fromInput textarea {
    width: 100%;
    height: auto;
    outline: none;
    border-radius: 10px;
    border: 1px solid #d7d2bd;
    background: #FFF;
    padding: 12px;
    color: #000;
    font-family: Plus Jakarta Sans;
    font-size: 16px;
    font-style: normal;
    font-weight: 500;
    line-height: 150%;
    letter-spacing: 0.48px;
    margin-bottom: 20px;
    resize: none;
}

.fromSubmitbtn {
    outline: none;
    border: none;
    text-decoration: none;
    cursor: pointer;
    width: 100%;
    height: auto;
    padding: 8px;
    border-radius: 8px;
    border: 1px solid #000;
    background: #000;
    color: #ffffff;
    text-align: center;
    font-family: 'EB Garamond', serif;
    font-size: 22px;
    font-style: normal;
    /* font-weight: 700; */
    line-height: 150%;
    letter-spacing: 1.3px;
    transition-property: all;
    transition-duration: 500ms;
}

.fromSubmitbtn:hover {
    border: 1px solid #bf9c60;
    background: #bf9c60;
}

.fromSubmitbtn:focus {
    outline: none;
}

.fromInput input::placeholder {
    color: #aaa;
    font-weight: 400;
}

.fromInput textarea::placeholder {
    color: #aaa;
    font-weight: 400;
}
@media(max-width:991px){
    .ContactTitle p{
        font-size: 20px;
    }
    .contactLeft p {
        font-size: 17px;
    }
    .contmap iframe{
        margin-top: 8px;
        height: 277px;
    } 
    .ContactBanner{
        height: 415px;
    }   
}
@media(max-width:767px){
    .contactPageMain .row{
        gap: 30px;
    }
    .contactMainSection {
        margin-top: 45px;
    }
    .ContactBanner {
        height: 355px;
    }
    .contactMainSection {
        margin-top: 35px;
    }
    .ContactTitle p {
        font-size: 18px;
    }
}
@media(max-width:575px){
    .contactMainSection {
        margin-top: 45px;
    }
    .ContactBanner {
        height: 290px;
    }
    .ContactTitle h3 {
        font-size: 30px;
    }
}
@media(max-width:480px){
    .ContactBanner {
        height: 245px;
    }
    .ContactTitle h3 {
        font-size: 25px;
    }
    .ContactTitle p {
        font-size: 16px;
    }
    .contactMainSection {
        margin-top: 35px;
    }
    .contactUsTitle h3 {
        font-size: 28px;
    }
    .contactLeft p {
        font-size: 15px;
    }
    .contmap iframe{
        height: 235px;
    }    
    .contactUsFromTitle h3 {
        font-size: 29px;
        margin-bottom: 25px;
    }
    .fromInput input{
        padding: 9px;
        font-size: 14px;  
    }
    .fromSubmitbtn{
        font-size: 19px;
    }
}