.bradecomeSection{
    width: 100%;
    height: auto;
    padding: 40px 0 0;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    gap: 10px;
}

.bradecomeSection a{
   font-size: 18px;
   color: #1e1e1e;
   font-weight: 600;
}
.bradecomeSection .active{
    color: #bf9c60;
}

.rgsForm{
    width: 100%;
    max-width: 800px;
    height: auto;
    margin: 0 auto;
}

.inputname select {
    height: 50px;
    width: 100%;
    padding: 8px 15px 8px 16px;
    color: #969696;
    border: 1px solid #d7d2bd;
    height: 50px;
    border-radius: 4px;
    background-color: transparent;
    margin-bottom: 20px;
}
.inputname{
    width: 100%;
    height: auto;
    display: flex;
    flex-direction: column;
}
.inputname label{
    font-size: 17px;
    font-family: 'Abel', sans-serif;
    margin-bottom: 0px;
    /* font-weight: 600; */
    letter-spacing: 1.2px;

}
.inputname label::placeholder{
    color: #aaa;
    font-weight: 400;
}
.inputname input{
    height: 50px;
    width: 100%;
    padding: 8px 15px 8px 16px;
    color: #969696;
    border: 1px solid #d7d2bd;
    height: 50px;
    border-radius: 4px;
    background-color: transparent;
    margin-bottom: 20px;
}
.commentSection{
    width: 100%;
    height: auto;
    display: flex;
    flex-direction: column;
}
.commentSection label{
    font-size: 19px;
    font-family: 'Abel', sans-serif;
    margin-bottom: 0px;
    /* font-weight: 600; */
    letter-spacing: 1.2px;
}
.commentSection textarea{
    width: 100%;
    height: 200px;
    padding: 8px 15px 8px 16px;
    color: #969696;
    border: 1px solid #d7d2bd;
    border-radius: 4px;
    background-color: transparent;
    resize: none;
}
.commentSection textarea::placeholder{
    color: #aaa;
    font-weight: 400;
}
.bookingcontinu{
    width: 100%;
    height: auto;
    margin: 30px 0px 20px;
    display: flex;
    align-items: center;
    justify-content: end;
}
.BookingcontinuBtn{
    outline: none;
    border: none;
    text-decoration: none;
    width: 170px;
    height: 50px;
    font-size: 16px;
    background-color: #1e1e1e;
    border-radius: 7px;
    color: #FFF;
    text-transform: uppercase;
}
.BookingcontinuBtn i{
    margin-left: 10px;
}
@media (max-width: 767px){

}
@media (max-width: 480px){
    .commentSection label{
        font-size: 16px;
    }
    .commentSection textarea {
        padding: 9px;
        font-size: 13px;
    }
    .inputname label {
        font-size: 15px;
    }
    .inputname input{
        font-size: 13px;
    }
    .inputname select{
        margin-bottom: 15px;
        height: 40px;
    }
}